<script>
import { mapGetters } from "vuex";
// import Detailed from "../components/tables/Detailed";
import Card from "../components/core/Card";
export default {
  components: {
    // Detailed,
    Card,
  },
  data() {
    return {
      search: "",
      date: new Date().toISOString().substr(0, 10),
      menu: false,
    };
  },
  computed: {
    ...mapGetters([
      "formLoading",
      "cardFormLoading",
      "dtrFormLoading",
      "eTriageVisitors",
      "eTriageERVisitors",
    ]),
  },
  mounted() {
    if (!localStorage.isLoggedIn) {
      this.$router.push("/");
    }
    this.getETriageOtherUsersToday();
  },
  methods: {
    async changeDate(){
      this.$refs.menu.save(this.date)
      await this.getETriageOtherUsersToday();
    },
    async getETriageOtherUsersToday() {
      await this.$store.dispatch("getETriageOtherUsers",this.date);
    },
  },
};
</script>

